// previiew projects
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    previewText: {
      type: String,
    },
    thumbnail: {
      type: String,
    },
    srcImage: {
      type: String,
      default: '/assets/images/editorimg.jpg',
    },
  },
  methods: {
    selectProject(item) {
      this.$emit('selectProject', item)
    },
  },
}
